<template>
  <section class="home-section">
    <section-header>
      {{title}}
      <template slot="right">
        <transition
          appear
          :enter-active-class="`${haveSeenInfo ? '' : 'animated bounce'}`"
          :leave-active-class="`${haveSeenInfo ? '' : 'animated bounce'}`"
        >
          <button @click="showInfoFunc" class="infoBtn">
            <img src="../assets/image/info.svg" alt="">
          </button>
        </transition>
      </template>
    </section-header>
    <section-content>
      <ul v-if="groups.length > 0">
        <li is="group-item"
          v-for="group in groups"
          :key="group.pk"
          :pk="group.pk"
          :title="group.title"
        ></li>
      </ul>
      <p class="sign" v-else>
        <br></p>
    </section-content>
    <action-btn url="/group/add">
      <img src="../assets/image/add-btn.svg" alt="">
    </action-btn>
    <transition
      enter-active-class="animated slideInUp"
      leave-active-class="animated slideOutDown"
    >
      <router-view></router-view>
    </transition>
    <info v-if="showInfo" @closeInfo="showInfo = false"></info>
  </section>
</template>

<script>
// @ is an alias to /src
import sectionHeader from '@/components/sectionHeader.vue'
import sectionContent from '@/components/sectionContent.vue'
import info from '@/components/info.vue'
import actionBtn from '@/components/actionBtn.vue'
import groupItem from '@/components/group/groupItem.vue'

import { mapState } from 'vuex'

export default {
  name: 'group',
  props: {
    title: String
  },
  data () {
    return {
      showInfo: false
    }
  },
  computed: {
    ...mapState([
      'groups',
      'haveSeenInfo'
    ])
  },
  methods: {
    showInfoFunc () {
      this.showInfo = true
      if (!this.haveSeenInfo) {
        this.$store.commit('clickedInfo')
      }
    }
  },
  created () {
    this.$store.commit('clearCheckArray')
  },
  components: {
    sectionHeader,
    sectionContent,
    groupItem,
    actionBtn,
    info
  }
}
</script>

<style>
.home-section {
  animation-duration: 0.3s;
  width: 100%;
}
ul {
  padding: 0;
}
.sign {
  color: #04724d;
  font-weight: normal;
  font-size: 1.2rem;
  text-align: center;
}
.infoBtn {
  animation-duration: 0.5s;
  animation-delay: 0.5s;
}
</style>
